import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Button, Form, FormGroup, Input, Label } from 'reactstrap';

export default class MenuItemEditFormModule extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.item.name,
            url: this.props.item.url,
            blank: this.props.item.target === '_blank',
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.item !== this.props.item) {
            this.setState((state) => ({
                name: this.props.item.name,
                url: this.props.item.url,
                blank: this.props.item.target === '_blank',
            }));
        }
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value,
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        const item = this.props.item;
        item.name = this.state.name;
        item.url = this.state.url;
        item.target = this.state.blank ? '_blank' : '';
        this.props.onSubmit(item);
    }

    handleCancel(event) {
        this.props.onCancel();
    }

    render() {
        const isBlank = this.props.item.target === '_blank' ? 'checked="checked' : '';
        return (
            <div>
                <Form onSubmit={this.handleSubmit}>
                    <FormGroup>
                        <Label for="item_name">{Translator.trans('menu_item.property.name')}</Label>
                        <Input type="text" name="name" value={this.state.name} id="item_name" onChange={this.handleInputChange} />
                    </FormGroup>
                    <FormGroup>
                        <Label for="item_url">{Translator.trans('menu_item.property.url')}</Label>
                        <Input type="text" name="url" value={this.state.url} id="item_url" onChange={this.handleInputChange} />
                    </FormGroup>
                    <FormGroup check>
                        <Label check for="item_target">
                            <Input
                                type="checkbox"
                                name="blank"
                                checked={this.state.blank}
                                id="item_target"
                                onChange={this.handleInputChange}
                            />
                            {Translator.trans('menu_item.property.new_window')}
                        </Label>
                    </FormGroup>
                    <Button type="submit" className="pull-right" color="primary">
                        {Translator.trans('menu_item.edit_form.save')}
                    </Button>
                    <Button type="button" onClick={this.handleCancel}>
                        {Translator.trans('menu_item.edit_form.cancel')}
                    </Button>
                </Form>
            </div>
        );
    }
}

MenuItemEditFormModule.propTypes = {
    item: PropTypes.object,
    onSubmit: PropTypes.func,
    onCancel: PropTypes.func,
};



